/* 상단 */
#top{width: 100%; height: 63px; position: relative;}
#top div{position: absolute; top: 0; width: 100%; height: 63px; background-color: #0D4633;}
#top h1{
    width: auto; 
    height: 63px; 
    line-height: 63px; 
    position: absolute; 
    left: 50%; 
    transform: translate(-50%,0); 
    color: #ffffff; 
    font-family: NotoM,sans-serif; 
    font-size: 20px; 
    font-weight: normal;}/*마이페이지*/
#top p{
    cursor: pointer; 
    position: absolute; 
    right: 25px; 
    top: 50%; 
    transform: translate(0,-50%); 
    width: 25px; 
    height: 25px;}/*알림*/
#top p img{width: 25px; height: 25px;}

#member_info_all{
    width: 96%; 
    overflow: hidden; 
    margin: 23px auto 0 auto; 
    padding-bottom: 40px;
    border-radius: 20px;
    background-color: #ffffff;
    box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.20);}
#member_info{width: 100%; overflow: hidden; padding: 10% 2%;}
.member_info{float: left; width: auto; height: 70px; margin-left: 2.5%;}
.membership{
    width: 86px; 
    height: 30px; 
    background-color: #0D4633; 
    font-family: NotoM,sans-serif;
    font-size: 14px; 
    color: #ffffff; 
    border-radius: 8px;
    text-align: center;
    line-height: 30px;}/*뚜쥬프렌즈*/
.user_name{
    width: 100%; 
    font-weight: bold; 
    font-size: 23px; 
    font-family: NotoM,sans-serif; 
    margin-top: 10px;}/*OOO님 안녕하세요*/
.membership_barcode{
    float: right; 
    width: 160px; 
    height: 70px; 
    margin-right: 4.5%; 
    cursor: pointer;}/*바코드*/
.membership_barcode p >img{width: 100%; height: 100%; margin-top: 10px;}
.membership_barcode p{width: 100%; line-height: 1.2em; font-size: 12px;}

#member_coupons{width: 100%; height: 38px; padding-left: 2%; box-sizing: border-box;}
#member_coupons li{float: left; width: 35%; height: 38px;}
#member_coupons li:first-child{width: 26%;}/*마이쿠폰*/
#member_coupons li > div{width: 135px; overflow: hidden; margin: 0 auto;}
#member_coupons li:first-child > div{width: 95px;}/*마이쿠폰*/
#member_coupons li > div p{float: left; width: 38px; height: 38px;}
#member_coupons li > div div{float: right; line-height: 1.3em; cursor: pointer;}
#member_coupons li > div div span{font-size: 14px; display: block;}
#member_coupons li > div div strong{color: #ED7200}


/* 주문내역 */
#order_history_wrap{
    width: 100%; 
    overflow: hidden; 
    background-color: rgba(13, 70, 51, 0.03); 
    margin: 35px 0;}
#order_history{width: 100%; padding: 21px 30px; box-sizing: border-box; position: relative;}
#order_history h2{width: 100%; font-size: 20px; margin-bottom: 20px;}/*주문 내역*/
.od_history{width: 100%; height: 80px;}
.od_history li{
    float: left; 
    width: 20%; 
    margin-left: 5%;
    padding-top: 55px; 
    box-sizing: border-box; 
    text-align: center; 
    position: relative;}
.od_history li:nth-child(1){background: url(../../public/images/takeaway_ico_b.png) no-repeat top 6px center; background-size: 43px 43px; margin-left: 2.5%;}/*픽업*/
.od_history li:nth-child(2){background: url(../../public/images/delivery_ico_b.png) no-repeat top 6px center; background-size: 45px 45px;}/*딜리버리*/
.od_history li:nth-child(3){background: url(../../public/images/calendar_ico_b.png) no-repeat top 8px center; background-size: 40px 40px;}/*사전예약*/
.od_history li:nth-child(4){background: url(../../public/images/voucher.png) no-repeat top 8px center; background-size: 42px 42px;}/*상품권*/
.od_history li::after{
    content: ""; 
    position: absolute; 
    width: 1px; 
    height: 60px;
    top: 50%;
    right: -15%;
    transform: translate(0,-50%); 
    background-color: #eaeaea;}
.od_history li:nth-child(4)::after{width: 0; height: 0; background-color: transparent;}
.od_history li a{font-family: NotoM,sans-serif; font-size: 15.5px; color: #444444}
.view_more{position: absolute; top: 21px; right: 30px;}/*자세히 보기+*/
.view_more a{color: #666666; font-size: 14px; text-decoration: underline;}

/* 쿠폰 */
#coupon_wrap{width: 87%; overflow: hidden; margin: 0 auto 35px auto;}
#coupon_wrap h2{width: 100%; font-size: 20px; margin-bottom: 25px;}/*쿠폰*/
.coupon_list li{width: 100%; line-height: 3em;}
.coupon_list li a{color: #000000; font-size: 16px;}

/* 내 정보 관리 */
#myinfo_wrap{width: 87%; overflow: hidden; margin: 0 auto;}
#myinfo_wrap h2{width: 100%; font-size: 20px; margin-bottom: 25px;}/*내 정보 관리*/
.myinfo_list li{width: 100%; line-height: 3em;}
.myinfo_list li a{color: #000000; font-size: 16px;}

.logout{
    display: block; 
    margin-top: 80px; 
    padding-bottom: 180px;
    text-align: center;}/*로그아웃*/
.logout a{
    font-family: NotoM,sans-serif; 
    font-size: 16px; 
    color: #666666;
    text-align: center; 
    text-decoration: underline;}/*로그아웃*/




