/* font */
@font-face {
    font-family: NotoKR;
    src:url('../public/font/NotoSansKR-Regular.woff');    
}
@font-face {
    font-family: NotoM;
    src:url('../public/font/NotoSansKR-Medium.woff');    
}

html,body{height: 100%;}
button{outline: none; border: none;}
address{font-style: normal;}
body,a{-webkit-user-select: none; -moz-user-select: none;-ms-user-select: none;user-select: none; font:normal 15px/1.5em NotoKR,sans-serif; color:#333333;}


/* 메인메뉴 하단고정 */
#main_menu{
    clear:both; 
    width:100%; 
    height:80px; 
    position: fixed; 
    bottom: 0; 
    left: 0; 
    z-index: 1000; 
    background-color: #ffffff; 
    box-shadow: 0px 3px 5px 3px rgba(85, 85, 85, 0.3);}
#main_menu ul{width:100%; height:80px;}
#main_menu ul li{
    float:left; 
    width:25%; 
    height: 80px; 
    line-height: 115px; 
    text-align: center; 
    background-position: center 15px; 
    background-repeat: no-repeat;}
#main_menu ul li:nth-child(1){background-image: url(../public/images/navi_home.png); background-size: auto 25px;}
#main_menu ul li:nth-child(2){background-image: url(../public/images/navi_product.png); background-size: auto 25px;}
#main_menu ul li:nth-child(3){background-image: url(../public/images/navi_shop.png); background-size: auto 25px;}
#main_menu ul li:nth-child(4){background-image: url(../public/images/navi_mypage.png); background-size: auto 25px;}
#main_menu ul li a{display: block; width:100%; height: 80px; font-size: 16px; line-height: 112px;}

.active{background-position: center 15px; background-repeat: no-repeat; color: #333333; font-family: NotoM,sans-serif;}
#main_menu ul li:nth-child(1) .active{background-image: url(../public/images/home.png); background-size: auto 25px;}
#main_menu ul li:nth-child(2) .active{background-image: url(../public/images/bread.png); background-size: auto 25px;}
#main_menu ul li:nth-child(3) .active{background-image: url(../public/images/store.png); background-size: auto 25px;}
#main_menu ul li:nth-child(4) .active{background-image: url(../public/images/mypage.png); background-size: auto 25px;}


/* 퀵메뉴 */
#quick_menu{position: fixed; bottom: 100px; right: 3.3%; width: 60px; z-index: 9999;}
.quick{display: none;}
.quick_show{position: absolute; bottom: 65px; display: block;}
.quick li,.quick_show li{
    cursor: pointer;
    width: 60px; 
    height: 60px; 
    border-radius: 50%; 
    background-color: #ffffff; 
    box-shadow: 0 3px 4px 0 rgba(0,0,0,0.25); 
    margin-bottom: 8px; position: relative; 
    background-repeat: no-repeat;
    background-position: center;}
.quick li:nth-child(1),.quick_show li:nth-child(1){background-image: url(../public/images/takeaway_ico_b.png); background-size: 32px 32px;}
.quick li:nth-child(2),.quick_show li:nth-child(2){background-image: url(../public/images/delivery_ico_b.png); background-size: 32px 32px;}
.quick li:nth-child(3),.quick_show li:nth-child(3){background-image: url(../public/images/calendar_ico_b.png); background-size: 30px 30px;}
.quick li:nth-child(4),.quick_show li:nth-child(4){background-image: url(../public/images/coupon1.png); background-size: 32px 32px;}
.quick li:nth-child(5),.quick_show li:nth-child(5){background-image: url(../public/images/event_ico.png); background-size: 27px 27px;}
.quick li a, .quick_show li a{
    font-family: NotoKR,sans-serif;
    color: #ffffff;
    font-size: 14px; 
    position: absolute; 
    left: -150%; 
    top: 50%; 
    transform: translate(0,-50%); 
    width: 80px; 
    text-align: right;}
#quick_menu p.plus_btn{
    cursor: pointer;
    width: 60px; 
    height: 60px; 
    border-radius: 50%; 
    box-shadow: 0px 0px 4px 1px rgba(0,0,0,0.25); 
    background: #ffffff url(../public/images/plus.png) no-repeat center;}/*+*/
#quick_menu p.plus_btn a{font-size: 0;}
#quick_menu p.plus_btn img{width: 60px; height: 60px; border-radius: 50%;}

/* 퀵메뉴 클릭시 bg */
.bg{display: block; position: fixed; left: 0; top: 0; z-index: 999; background-color: rgba(0,0,0,0.7); width: 100%; height: 100%;}
.hideBg{display: none;}
