/* 상단 */
.store{width: 100%; margin: 0 auto; padding: 0 4.5%; box-sizing: border-box;  overflow: hidden;}
.store li{width: 100%; overflow: hidden; position: relative; margin-bottom: 25px;}
.store li p.store_img{float: left; border-radius: 15px; width: 25%; margin-top: 4px; cursor: pointer;}/*매장 이미지*/
.store li p.store_img img{ width: 95px; height: 95px; border-radius: 15px;}
.store li div{float: left; width: auto; margin-left: 5%; margin-top: 17px; cursor: pointer;}/*매장 txt*/
.store_name{font-family: NotoM,sans-serif;  font-size: 16px;}/*매장명*/
.store_address{font-size: 12px; color: #999999;}/*매장주소*/
.store_time{font-size: 12px; color: #666666;}/*매장운영시간*/

.star_mark{
    position: absolute; 
    right: 0; 
    top: 0; 
    color: #cccccc; 
    font-size: 24px; 
    cursor: pointer; 
    width: 25px; 
    height: 25px;}/*★*/
.star_mark_color{
    position: absolute; 
    right: 0; 
    top: 0; 
    color: #F5D52B; 
    font-size: 24px; 
    cursor: pointer; 
    width: 25px; 
    height: 25px;}

.store li p.distance{position: absolute; right: 0; bottom: 0; font-family: NotoM,sans-serif;  font-size: 15px;}/*거리*/

#info{
    width: 90%; 
    margin: 0 auto; 
    margin-top: 25px; 
    height: 20px; 
    padding-bottom: 280px; 
    font-size: 14px; 
    color: #999999; 
    background: url(../../public/images/alert_ico.png) no-repeat left top 4px; 
    background-size: 18px 18px; 
    padding-left: 22px; 
    box-sizing: border-box;}/*현재 있는 주소의 근처 매장만 보여요.*/



/* 380px 이하*/
@media screen and (max-width : 380px ){
    
    .store li{width: 100%; overflow: hidden; position: relative; margin-bottom: 20px;}
    .store li p.store_img{float: left; border-radius: 15px; margin-right: 5%; cursor: pointer;}/*매장 이미지*/
    .store li p.store_img img{ width: 90px; height: 90px; border-radius: 15px;}
    .store li div{float: left;  width: 65%; margin-top: 18px; cursor: pointer;}/*매장 txt*/
    .store_name{font-family: NotoM,sans-serif;  font-size: 16px; margin-bottom: 10px;}/*매장명*/
    .store_address{display: none;}/*매장주소*/
    .store_time{font-size: 12px; color: #666666;}/*매장운영시간*/
    .star_mark{
        position: absolute; 
        right: 0; top: 12px; 
        color: #cccccc; 
        font-size: 24px; 
        cursor: pointer; 
        width: 25px; 
        height: 25px;}/*★*/
    .star_mark_color{
        position: absolute;
        right: 0; 
        top: 12px; 
        color: #F5D52B; 
        font-size: 24px; 
        cursor: pointer; 
        width: 25px; 
        height: 25px;}
    .store li p.distance{position: absolute; right: 0; bottom: 0; font-family: NotoM,sans-serif;  font-size: 14px;}/*거리*/

}