/* 상단 */
#top{width: 100%; height: 135px; position: fixed; top: 0; z-index: 100;}
#top div:nth-of-type(1){width: 100%; height: 63px; background-color: #0D4633; position: relative;}
#top div:nth-of-type(1) h1{
    width: auto; 
    height: 63px; 
    line-height: 63px; 
    position: absolute; 
    left: 50%; 
    transform: translate(-50%,0); 
    color: #ffffff; 
    font-family: NotoM,sans-serif; 
    font-size: 20px; 
    font-weight: normal;}/*매장찾기*/
#top div:nth-of-type(1) p{
    cursor: pointer;
    position: absolute; 
    right: 30px; 
    top: 50%; 
    transform: translate(0,-50%); 
    width: 20px; 
    height: 20px;  
    color: #ffffff; }/*검색*/
#store_list{width: 100%; height: 60px; background-color: #ffffff;}/*가까운 매장, 즐겨찾는 매장, 최근 이용한 매장*/
#store_list li{float: left; width: 33.3%; height: auto;}
#store_list li a{
    display: block; 
    text-align: center; 
    width: 100%; 
    height: 60px; 
    line-height: 60px; 
    font-family: NotoM,sans-serif; 
    font-weight: normal; 
    font-size: 17px; 
    color:#666666; 
    box-sizing: border-box;}
#store_list li a.active{
    font-family: NotoKR,sans-serif; 
    font-weight: bold; 
    color:#333333; 
    border-bottom: 2px solid #666666;}


#order_select{
    background-color: #ffffff;
    width: 100%; 
    height: 67px;
    padding: 12px 15px;
    border-bottom: 1px solid #cccccc;
    box-sizing: border-box;}/*픽업, 딜리버리, 사전예약*/
#order_select ul{float: left; width: auto;}
#order_select ul li{
    cursor: pointer;
    float: left;
    height: 23px;
    width: auto; 
    margin-right: 12px; 
    padding: 8px 10px 8px 40px; 
    border-radius: 10px; 
    border: 1px solid #999999;}
#order_select ul li:nth-child(1){background: url(../../public/images/takeaway_ico_b.png) no-repeat left 10px center; background-size: 23px 23px;}/*픽업*/
#order_select ul li:nth-child(2){background: url(../../public/images/delivery_ico_b.png) no-repeat left 10px center; background-size: 23px 23px;}/*딜리버리*/
#order_select ul li:nth-child(3){background: url(../../public/images/calendar_ico_b.png) no-repeat left 10px center; background-size: 21px 21px; margin-right: 0;}/*사전예약*/
#order_select ul li a{color: #666666; font-family: NotoM,sans-serif; font-size: 14px;}

#order_select ul li:nth-child(1).sel{background: #0D4633 url(../../public/images/takeaway_ico_w.png) no-repeat left 10px center; background-size: 23px 23px;;}/*픽업*/
#order_select ul li:nth-child(2).sel{background: #0D4633 url(../../public/images/delivery_ico_w.png) no-repeat left 10px center; background-size: 23px 23px;;}/*딜리버리*/
#order_select ul li:nth-child(3).sel{background: #0D4633 url(../../public/images/calendar_ico_w.png) no-repeat left 10px center; background-size: 23px 23px;;}/*사전예약*/
#order_select ul li.sel a{color: #ffffff;}



.filter{float: right; border: none; width: 20px; height: 20px; margin-top: 10px; cursor: pointer;}/*필터*/
.filter img{width: 20px; height: 20px;}


#store_all{width: 100%; overflow: hidden; padding-top: 230px;}


.map_btn{
    cursor: pointer;
    position: fixed; 
    bottom: 168px; 
    left: 50%; 
    transform: translate(-50%,0); 
    width: auto; 
    padding-left: 48px;
    padding-right: 22px;
    height: 50px; 
    background: #ffffff url(../../public/images/map_ico.png) no-repeat left 20px center;
    background-size: 20px 20px;
    border: 1px solid #0D4633;
    border-radius: 25px;
    box-shadow: 0px 0px 4px 0px #999999;
    font-family: NotoM,sans-serif;  
    font-size: 16px; 
    color:#0D4633;}/*지도*/

/* 장바구니 */
#order_tab{
    width: 100%; 
    height: 63px; 
    background-color: #0D4633; 
    position: fixed; 
    bottom: 80px; 
    left: 0;
    z-index: 100;
    box-sizing: border-box;}
#order_tab div{height: 100%; margin: 0 auto; box-sizing: border-box; padding: 15px 0 15px 23px; }
#order_tab div p{float: left;}
.shopping_bag{width: 30px; height: 30px; margin-right: 32px; position: relative; cursor: pointer;}
.shopping_bag img{width: 100%; height: 100%; margin-right: 16px;}
.shopping_bag::after{
    content: "0";
    color: #ffffff; 
    background-color: #ED7200; 
    border-radius: 50%; 
    width: 20px; 
    height: 20px; 
    font-size: 13px;
    text-align: center; 
    line-height: 20px; 
    position: absolute; 
    top: -7px; 
    right: -7px;}
.store_txt{
    font-family: NotoM,sans-serif; 
    font-size: 14px; 
    color: #ffffff; 
    height: 30px; 
    line-height: 30px; 
    margin-right: 15px; 
    border-bottom: 1px solid #cccccc; 
    cursor: pointer;}
.store_more_view{
    width: 15px; 
    height: 9px; 
    line-height: 15px; 
    margin-top: 8px; 
    cursor: pointer;}
.store_more_view img{width: 100%; height: 100%;}




