
/* 상단 */
#top{
    width: 100%; 
    height: 390px; 
    position: relative; 
    background: url(../../public/images/main_img.jpg) no-repeat center; 
    background-size: cover;}
#top div{position: absolute; top: 0; width: 100%; height: 63px; background-color: #0D4633;}
#top h1{
    width: 180px; 
    height: 25px; 
    position: absolute; 
    left: 50%; 
    top: 50%; 
    transform: translate(-50%,-50%);}/*TOUS les JOURS 로고*/
#top h1 img{width: 180px; height: 25px;}
#top p{
    cursor: pointer; 
    position: absolute; 
    right: 25px; 
    top: 50%; 
    transform: translate(0,-50%); 
    width: 25px; 
    height: 25px;}/*알림*/
#top p img{width: 25px; height: 25px;}

#member_all{
    width: 100%; 
    overflow: hidden;
    padding-bottom: 20px; 
    background-color: white; 
    border-radius: 60px 60px 30px 30px; 
    box-shadow: 0px 3px 6px 0px rgba(0, 0, 0, 0.10); 
    position: absolute; 
    top: 328px;}
#member_wrap{width: 90%; margin: 0 auto;}
.member_info{width: 95%; margin: 36px auto 0 auto; height: 165px;}
.member_info > div:first-child{position: relative; width: 98%; overflow: hidden; margin: 0 auto;}
.membership_all{float: left; width: 100%; margin-top: 12px;}
.membership_all > .user_name{font-size: 26px; color: #0D4633; font-weight: bold; margin-bottom: 10px;}/*OOO님*/
.membership_all > .membership{
    font-size: 17px; 
    color: #666666; 
    font-weight: bold; 
    background: url(../../public/images/arrow_slide.png) no-repeat 95px center; 
    background-size: 7px 13px; 
    margin-bottom: 15px; 
    cursor: pointer;}/*뚜쥬프렌즈*/
.membership_barcode{
    position: absolute; 
    right: 0; 
    top: 50%; 
    transform: translate(0,-50%); 
    width: 25%; 
    cursor: pointer; 
    min-width: 140px;}/*바코드*/
.membership_barcode > p:first-child{width: 100%; max-width: 200px; min-width: 140px; height: 50px;}
.membership_barcode > p:last-child{width: 100%; font-size: 13px; color: #444444; min-width: 130px;}
.membership_barcode img{width: 100%; height: 100%;}

.member_score_wrap{width: 100%; height: 50px; margin-top: 10px; position: relative;}/*멤버십 점수*/
.member_score_wrap::before{
    content: ""; 
    height: 10px; 
    width: 100%; 
    background-color: rgba(114, 114, 114, 0.2); 
    display: block; 
    position: absolute; 
    top: 0; 
    left: 50%;
    transform: translate(-50%,0);
    border-radius: 20px;}
.member_score_wrap::after{
    content: ""; 
    height: 10px; 
    width: 13%; 
    background-color: rgba(13, 70, 51, 0.80);
    display: block; 
    position: absolute; 
    top: 0; 
    left: 0;
    border-radius: 20px;}
.member_score{
    width: 100%; 
    position: absolute; 
    top: 15px; 
    font-family: NotoM,sans-serif; 
    font-size: 13px; 
    height: 18px;}
.member_score span{width: auto; display: block;}
.member_score > span:nth-child(1){position: absolute; left: -1%; bottom: 0;}
.member_score > span:nth-child(2){position: absolute; left: 15%; bottom: 0;}
.member_score > span:nth-child(3){position: absolute; left: 40%; bottom: 0;}
.member_score > span:nth-child(4){position: absolute; right: -2%; bottom: 0;}
.membership_txt{ position: absolute; bottom: -5px; font-size: 12px; width: 100%; height: 18px; }/*뚜쥬패밀리까지...*/
.membership_txt strong{color: #ED7200}

#order_wrap{width: 92%; margin: 10px auto 0 auto; overflow: hidden;}/*주문 방법 선택*/
#order_wrap li{width: auto; height: 100%; cursor: pointer;}
#order_wrap li:nth-child(1){float: left;}/*딜리버리*/
#order_wrap li:nth-child(2){float: right;}/*픽업*/
#order_wrap li img{float: left; margin-top: 8px;}
#order_wrap li div {float: left; margin-left: 10px;}
#order_wrap li div h3{
    font-family: NotoM,sans-serif; 
    font-size: 20px; 
    font-weight: normal; 
    background: url(../../public/images/arrow_slide.png) no-repeat right center; 
    background-size: 5px 12px; 
    margin-top: 6px;}
#order_wrap li div p{font-size: 10px; line-height: 1.2em; margin-top: 3px;}

#coupon_wrap{
    clear: both; 
    width: 90%; 
    margin: 30px auto 0 auto; 
    background-color: #F4F4F4; 
    height: 150px; 
    border-radius: 10px;}
#coupon_wrap > div{
    text-align: center; 
    height: 150px; 
    padding-top: 36px; 
    font-size: 16px; 
    color: #666666; 
    box-sizing: border-box; 
    letter-spacing: 0.4px;} 
#coupon_wrap > div span{display: block;}
#coupon_wrap > div p{margin-bottom: 10px;}
#coupon_wrap a{text-decoration: underline; font-size: 14px;}


/* 즐겨찾는 매장 */
#bookmark_wrap{
    clear: both; 
    margin-top: 480px; 
    width: 100%; 
    overflow: hidden; 
    padding-bottom: 5px; 
    margin-bottom: 60px;}
#bookmark{width: 90%; height: 100%; margin: 0 auto;}
#bookmark h2{width: 100%; font-size: 20px; margin-bottom: 20px;}/* 즐겨찾는 매장 */
.bookmark_con{
    background-color: #ffffff; 
    width: 100%; 
    height: auto; 
    box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.10); 
    border-radius: 10px; 
    padding: 3% 5%; 
    box-sizing: border-box;}
.bookmark_con > div{width: 100%; overflow: hidden; position: relative; margin-bottom: 18px;}
.bookmark_con > div > p{float: left; width: 108px; height: 108px;}/*매장 사진*/
.bookmark_con > div > p img{width: 100%; height: 100%;}
.bookmark_con > div > div{float: right; width: 60%;}
.bookmark_con > div > div h3{font-size: 18px; font-weight: normal; font-family: NotoM,sans-serif; margin-top: 17px;}/*매장명*/
.bookmark_con > div > div p:nth-of-type(1){font-size: 14px; width: 100%; margin-top: 7px;}/*주소*/
.star_mark_color{position: absolute; right: 0; top: 3px; color: #F5D52B; font-size: 26px; cursor: pointer;}/*★*/
.star_mark{position: absolute; right: 0; top: 3px; color: #cccccc; font-size: 26px; cursor: pointer;}

.quick_order{width: 100%; overflow: hidden; border-top: 1px solid #cccccc; padding: 3% 0 3px 5%;}/*픽업,딜리버리,사전예약*/
.quick_order li{float: left; width: 33.3%; text-indent: 30%;}
.quick_order li:nth-child(1){background: url(../../public/images/takeaway_ico_b.png) no-repeat left 13% center; background-size: 25px 25px; text-indent: 35%;}/*픽업*/
.quick_order li:nth-child(2){background: url(../../public/images/delivery_ico_b.png) no-repeat left center; background-size: 25px 25px;}/*딜리버리*/
.quick_order li:nth-child(3){background: url(../../public/images/calendar_ico_b.png) no-repeat left center; background-size: 23px 23px;}/*사전예약*/
.quick_order li a{font-size: 15px; font-family: NotoM,sans-serif;}


/* > 더보기 공통*/
.more_view{position: absolute; right: 12px; top: 0; width: 9px; height: 18px;}
.more_view button{background-color: transparent; cursor: pointer;}

/* 신제품 소개 */
#new_product_wrap{clear: both; width: 100%; overflow: hidden; margin-bottom: 60px;}
#new_product{width: 90%; margin: 0 auto; position: relative;}
#new_product > h2{width: 100%; font-size: 20px; margin-bottom: 20px;}/* 신제품 소개 */
#new_product > p:nth-of-type(1){width: 100%; border-radius: 10px; margin-bottom: 20px; cursor: pointer;}
#new_product > p:nth-of-type(1) img{width: 100%; border-radius: 10px;}
.new_banner{width: 100%; border-top: 1px solid #cccccc; border-bottom: 1px solid #cccccc; padding: 18px 0;}
.new_banner p{margin-left: 1%; width: 94%; font-family: NotoM,sans-serif; font-size: 17px; margin-top: 8px;}

/* 나의 프리퀀시 */
#frequency_wrap{clear: both; width: 100%; overflow: hidden; margin-bottom: 60px;}
#frequency{width: 90%; margin: 0 auto; position: relative;}
#frequency > h2{width: 100%; font-size: 20px; margin-bottom: 20px;}/* 나의 프리퀀시 */
.frequency_stamp_wrap{background-color: #FFF9F0; border-radius: 10px; padding: 8%; position: relative;}
.frequency_stamp_wrap > h3{font-size: 20px; color: #926D4B;}/* FREQUENCY */
.frequency_stamp_wrap > p{
    font-family: NotoM,sans-serif; 
    font-size: 14px; color: #B49171; 
    margin-top: 5px; 
    border-bottom: 3px solid #D2C1B1; 
    padding-bottom: 18px; 
    margin-bottom: 17px;}/*이벤트 적립 기간 2024.02.01 ~ 2024.03.31*/
#fq_stamp{
    background-color: #ffffff; 
    border-radius: 10px; 
    width: 96%; 
    margin: 0 auto;
    overflow: hidden; 
    padding: 8% 3% 8% 6%; 
    box-sizing: border-box;}
.frequency_stamp_wrap  > img{
    position: absolute; 
    right: -10px; 
    bottom: 0; 
    width: 35%; 
    min-width: 100px; 
    max-width: 160px;}

/* 이벤트&멤버십 */
#event_membership_wrap{width: 100%; margin-bottom: 60px; overflow: hidden;}
#event_membership{width: 90%; margin: 0 auto; position: relative;}
#event_membership > h2{width: 100%; font-size: 20px; margin-bottom: 20px;}/* 이벤트&멤버십 */
.event_banner{width: 100%;}
#evt_ban{width: 100%;}
#evt_ban li{width: 100%; height: 100%; cursor: pointer;}
#evt_ban li img{width: 100%; height: 100%;}

/* 하단정보 */
#bottom_wrap{width: 100%; overflow: hidden; padding-bottom: 120px; background-color: #F0F0F0}
#bottom{width: 90%; margin: 0 auto;}
#bottom ul{width: 100%; height: 9px; margin-top: 36px;}
#bottom ul li{float: left; margin-right: 10px;}
#bottom ul li a{font-size: 12px; color: #777777}
#bottom_address{clear: both; margin-top: 25px;}
#bottom_address p{color: #999999; font-size: 12px; line-height: 2em;}
#bottom_address p span{display: block;}
#bottom_address p:last-child{margin-top: 25px;}




/* 380px 이하*/
@media screen and (max-width : 380px ){

    #top p{
        position: absolute; 
        right: 20px; 
        top: 50%; 
        transform: translate(0,-50%);
        width: 25px; 
        height: 25px;}/*알림*/
    #top p img{width: 25px; height: 25px;}


    /* 딜리버리,픽업 */
    #order_wrap{width: 96%; margin: 20px auto 0 auto; overflow: hidden;}
    #order_wrap li{width: 50%; height: 100%; cursor: pointer;}
    #order_wrap li:nth-child(1){float: left;}
    #order_wrap li:nth-child(2){float: right;}
    #order_wrap li div{
        width: 100%; 
        background: url(../../public/images/arrow_slide.png) no-repeat right 10% center; 
        background-size: 5px 12px;}
    #order_wrap li div h3{
        width: 100%;
        font-family: NotoM,sans-serif; 
        font-size: 20px; 
        font-weight: normal;
        background: none;}
    #order_wrap li img{display: none;}
    #order_wrap li div p{margin-top: 5px;}


    /* 즐겨찾는 매장 */
    #bookmark_wrap{
        clear: both; 
        margin-top: 470px; 
        width: 100%; 
        overflow: hidden; 
        padding-bottom: 5px; 
        margin-bottom: 60px;}
    .bookmark_con{padding: 3% 7%;}
    .bookmark_con > div > p{display: none;}
    .bookmark_con > div > div{width: 100%; margin-bottom: 8px;}
    .bookmark_con > div > div h3{font-size: 18px; font-weight: normal; font-family: NotoM,sans-serif;}
    .bookmark_con > div > div p:nth-of-type(1){font-size: 14px; width: 100%; margin-top: 15px;}
    .star_mark_color{position: absolute; right: 0; top: 15px; color: #F5D52B; font-size: 24px; cursor: pointer;}
    .star_mark{position: absolute; right: 0; top: 15px; color: #cccccc; font-size: 24px; cursor: pointer;}

    .quick_order{width: 100%; overflow: hidden; padding: 5% 0 3px 1%; border-top: 1px solid #cccccc;}/*픽업,딜리버리,사전예약*/
    .quick_order li{float: left; width: 33.3%; text-indent: 30%;}

    .quick_order li a{font-size: 14px; font-family: NotoKR,sans-serif;}

    /* 신제품 소개 */
    .new_banner p{width:90%; margin-left: 2%; font-size: 15px;}


    /* 나의 프리퀀시 */
    .frequency_stamp_wrap > p span{display: block;}

}