/* 상단 */
#top{width: 100%; height: 135px; position: fixed; top: 0; z-index: 100;}
#top div{width: 100%; height: 63px; background-color: #0D4633; position: relative;}
#top div h1{
    width: auto; 
    height: 63px; 
    line-height: 63px; 
    position: absolute; 
    left: 50%; 
    transform: translate(-50%,0); 
    color: #ffffff; 
    font-family: NotoM,sans-serif; 
    font-size: 20px; 
    font-weight: normal;}/*전체 상품*/
#top div p{
    cursor: pointer;
    position: absolute; 
    right: 30px; 
    top: 50%; 
    transform: translate(0,-50%); 
    width: 20px; 
    height: 20px;  
    color: #ffffff; }/*검색*/
#product_list{
    position: relative;
    background-color: #ffffff;
    width: 100%; 
    white-space:nowrap; 
    overflow:auto;  
    height: 41px;
    padding: 15px;
    border-bottom: 1px solid #cccccc;}/*ul*/
#product_list::-webkit-scrollbar{height: 0; display: none;}/*스크롤바 높이*/
#product_list::-webkit-scrollbar-track{background-color:transparent;}/*스크롤바 색상*/

#product_list li{
    display:inline-block;
    height: 23px;
    width: auto; 
    margin-right: 12px; 
    padding: 8px 14px; 
    border-radius: 10px; 
    border: 1px solid #666666;}
#product_list li a{color: #666666; font-family: NotoM,sans-serif; font-size: 15px;}
#product_list li.sel{background-color: #0D4633;}/*픽업*/
#product_list li.sel a{color: #ffffff;}




/* 제품 */
#product_wrap{
    clear: both; 
    width: 100%; 
    overflow: hidden; 
    padding-top: 135px; 
    padding-bottom: 100px;}
#product_wrap > div{
    width: 92%; 
    padding-top: 34px; 
    padding-bottom: 55px; 
    margin: 0 auto;}
#product_wrap > div p:first-child{float: left; font-family: NotoM,sans-serif; font-size: 18px;}/*총 435개*/
#product_wrap > div p:first-child span{font-family: NotoKR,sans-serif; font-weight: bold;}
#product_wrap > div p:last-child{float: right; cursor: pointer;}/*카테고리*/


.paddingTop{padding-top: 500px;}


.product{
    clear: both; 
    width: 100%; 
    font-size: 20px; 
    font-weight: bold; 
    overflow: hidden; 
    padding: 0 4.5%; 
    margin-bottom: 30px;
    box-sizing: border-box;
    border-bottom: 1px solid #eaeaea;}
.product li{
    width: 100%; 
    height: 95px;
    margin-bottom: 28px; 
    font-family: NotoM,sans-serif; 
    font-weight: normal; 
    font-size: 16px; 
    position: relative;}
.product li:first-child{margin-top: 35px;}

.product li div:nth-of-type(1){float: left; position: relative; border-radius: 15px;}/*img*/
.product li div:nth-of-type(1) img{float: left; position: relative; width: 95px; height: 95px; border-radius: 15px;}
.product li div:nth-of-type(2){float: left; margin-left: 20px; margin-top: 16px;}/*txt*/
.product li div:nth-of-type(2) p.product_name{color: #666666;}/*제품명*/
.product li div:nth-of-type(2) p.product_price{margin-top: 18px;}/*가격*/
.pd_tag{
    width: auto; 
    height: auto;
    z-index: 10; 
    position: absolute; 
    left: -12px; 
    top: -10px; 
    font-size: 13px; 
    background-color: #ED7200; 
    border-radius: 15px; 
    text-align: center; 
    padding: 5px 8px; 
    color: #ffffff;}/*NEW, BEST*/
.product_add{
    cursor: pointer;
    position: absolute; 
    bottom: 12px; 
    right: 0; 
    font-family: NotoM,sans-serif; 
    font-size: 0;
    color: #666666;
    width: 28px;
    height: 28px;
    background: #ffffff url(../../public/images/add.png) no-repeat center;
    border: 1px solid #cccccc;
    border-radius: 50%;}/*+*/


/* 장바구니 */
#order_tab{
    width: 100%; 
    height: 63px; 
    background-color: #0D4633; 
    position: fixed; 
    bottom: 80px; 
    left: 0;
    z-index: 100;
    box-sizing: border-box;}
#order_tab div{height: 100%; margin: 0 auto; box-sizing: border-box; padding: 15px 0 15px 23px; }
#order_tab div p{float: left;}
.shopping_bag{width: 30px; height: 30px; margin-right: 32px; position: relative; cursor: pointer;}
.shopping_bag img{width: 100%; height: 100%; margin-right: 16px;}
.shopping_bag span{
    color: #ffffff; 
    background-color: #ED7200; 
    border-radius: 50%; 
    width: 20px; 
    height: 20px; 
    font-size: 13px;
    text-align: center; 
    line-height: 20px; 
    position: absolute; 
    top: -7px; 
    right: -7px;}
.store_txt{
    font-family: NotoM,sans-serif; 
    font-size: 14px; 
    color: #ffffff; 
    height: 30px; 
    line-height: 30px; 
    margin-right: 15px; 
    border-bottom: 1px solid #cccccc; 
    cursor: pointer;}
.store_more_view{width: 15px; height: 9px; line-height: 15px; margin-top: 8px; cursor: pointer;}
.store_more_view img{width: 100%; height: 100%;}




